@use 'base/mixins' as m;
@use 'base/variables' as v;



.dashboard {

    display: flex;
    flex-direction: column;
    background-color: v.$blanco;
    //LAYOUT

    &__employee-id {
        display: block;

        @include m.tablet {
            display: none;
        }
    }

    &__employee-exp {
        display: none;

        @include m.tablet {
            display: block;
        }

        &::before {
            content: '-';
            display: block;
            width: .1rem;
            height: .1rem;
            position: relative;
            right: .9rem;
        }
    }




    &__grid {
        display: flex;
        flex: 1;

    }


    &__contenido {
        background-color: v.$contenido;
        padding: 10rem 3rem 3rem 2rem;
        width: 100%;


        @include m.tablet {
            padding: 10rem 1rem 0 26rem;
            // display: flex;
            // justify-content: center;
            // align-items: center;
        }
    }

    &__sidebar {

        width: 4.5rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
      


        @include m.tablet {
            width: 25rem;  
            position: fixed;
            bottom: 0;
            top: 6rem;
        }
    }



    //BARRA SUPERIOR

    &__header {
        padding: .7rem 0;
        position: fixed;
        top: 0;
        width: 100%;


    }

    &__empleado {
        display: flex;
        align-items: center;
        gap: 1rem;
        padding-right: 2rem;

        &--nombre {
            font-size: 1.5rem;




            @include m.tablet {
                display: flex;
                font-size: 1.7rem;
                gap: 1rem;
            }
        }


        &--texto {
            display: flex;
            flex-direction: column;
            line-height: 0;

        }

        &--foto {
            width: 4.8rem;
            height: 4.8rem;
            border-radius: 50%;
            overflow: hidden;

        }

    }

    &__submit--logout {

        background-color: transparent;
        border: none;
        font-size: 1.4rem;
        padding: 0;



    }

    &__logout {

        display: flex;
        align-items: center;
        gap: .5rem;
        font-size: 1.5rem;
        justify-content: space-between;
        width: 100%;




    }


    &__header-grid {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        padding: 0 2rem 0 1rem;

        @include m.tablet {
            flex-direction: row;
        }

    }

    &__logo {
        width: 15rem;
        display: none;

        @include m.tablet {
            display: block;
        }

    }


    &__form {
        display: flex;
        justify-content: flex-end;
        margin-top: -0.5rem;

        // @include m.tablet {
        //     justify-content: flex-end;
        // }
    }

    &__notification {
        position: absolute;

        &--pop {
            background-color: red;
            border-radius: 50%;
            width: 2.5rem;
            height: 2.5rem;
            margin: 0;
            padding: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            left: 3.5rem;
            bottom: 5rem;

            // @include m.tablet {

            //     right: 2.5rem;
            // }

            & a {
                color: white;
                font-size: 1.3rem;
                font-weight: v.$regular;
            }
        }
    }

    &__ip {
        display: none;
        position: sticky;
        bottom: 0;

        @include m.tablet {
            display: block;
            text-align: center;
            color: v.$blanco;

            & span {
                font-weight: v.$bold;
            }
        }
    }

    &__menu {
        position: sticky;
        top: 6.5rem;
    }


    //SIDEBAR

    &__enlace {


        display: flex;
        // flex-direction: column;
        align-items: center;
        padding: 1.5rem 1rem 2rem 1.3rem;
        transition: .3s background-color ease-in-out, .3s color ease-in-out, .3s border-radius ease-in-out;
        text-align: center;

        gap: 2.5rem;
        overflow: hidden;



        // &:hover{
        //     background-color: lighten(v.$admin, 20);
        //     color: v.$blanco;
        //     border-radius: 1rem;
        // }




    }



    &__icono {

        //    font-size: 30rem;
        // color: v.$blanco;
        width: 1rem;
    }

    &__menu-texto {
        font-size: 1.1rem;
        text-transform: uppercase;
        font-weight: v.$bold;
        margin-top: .5rem;
    }


    &__lista {
        list-style: none;
        padding: 0;
        padding-left: .8rem;
        margin: 0;
        // border-left: 1px solid v.$blanco;
        margin-left: 1.7rem;

    }


    &__menus {
        display: flex;
        flex-direction: column;
        justify-content: space-between;

    }


    /////////////////////////
    /// 
    /// 
    /// 
    /// 
    /// 

}

//dashboard



.menu-principal:hover {
    cursor: pointer;
}


ul[class*='-hidden'] {
    height: 0;
    overflow: hidden;
    
}



.dash-empleado {

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 3rem;

    &__imagen {

        width: 10rem;
        height: 10rem;
        border-radius: 50%;
        overflow: hidden;
        margin: 0 auto;
        border: 1rem solid v.$blanco;

        @include m.tablet {
            width: 30rem;
            height: 30rem;
        }

    }

    &__informacion-tickete {

        display: grid;
        grid-template-columns: auto auto;
        gap: 2rem;


        @include m.desktop {
            display: flex;

            justify-content: space-evenly;
            gap: 3rem;
            flex-direction: row;
        }





        &--total {
            background-color: blue;

        }

        &--abiertos {
            background-color: rgb(236, 64, 64);

        }

        &--pausados {
            background-color: lightskyblue;

        }

        &--cerrados {
            background-color: green;
        }
    }

    &__informacion-texto {
        text-align: center;

        &--leyenda {

            margin-top: 8rem;
            font-size: 1.6rem;

            @include m.tablet {
                margin-top: 5rem;
                font-size: 1.8rem;
            }


        }
    }



}


.tickete {

    &--info {
        display: flex;
        flex-direction: column;
        align-items: center;
        border-radius: 2rem;
        padding: 0 2rem;
        width: auto;
        height: 12rem;

        @include m.tablet {
            font-size: 3rem;
            width: 29rem;
            height: auto;

        }

        text-align: center;

        & p:first-child {
            // margin-bottom: 2rem;
            //   border: 1px solid purple;

        }

        & p:nth-child(2) {
            font-weight: v.$bold;
            // border: 1px solid purple;
            padding: 0;
            margin: 0;
            font-size: 5rem;

            @include m.tablet {

                font-size: 8rem;
            }
        }


    }

    &--texto {
        color: white;
        margin: 1rem;



    }

}

.folio{
    color: green;
    font-weight: v.$bold;
    font-size: 2rem;
}